                            unfocusTableBody(table);

                            if (targetToFocusChildren && !targetToFocusChildren.length) {
                                elementIndex = undefined;
                                tempTargetIndex = undefined;
                                targetToFocusChildren = table.find('.thead th:first');
                            }
